/**
 * Credit System settings page - deposits, withdrawals, privacy period (with tabs)
 */
import { useState, useCallback, useEffect, useMemo } from 'react';
import type { SystemSetting } from '../../../types';
import { LoadingSpinner } from '../../shared/LoadingSpinner';
import { ErrorMessage } from '../../shared/ErrorMessage';
import { useSettingsAutosave } from '../../../hooks/useSettingsAutosave';
import { SettingsSection } from './settingsInputs';
import { AutosaveStatus } from './AutosaveStatus';

type CreditTab = 'deposits' | 'tokens' | 'fees' | 'privacy' | 'treasury';

interface TabSection {
  title: string;
  description?: string;
  keys: string[];
}

interface TabConfig {
  id: CreditTab;
  label: string;
  /** Categories to pull settings from */
  categories: string[];
  /** Specific keys to include (if empty, include all from categories) */
  includeKeys?: string[];
  /** Specific keys to exclude */
  excludeKeys?: string[];
  /** Additional sections rendered below the main settings */
  sections?: TabSection[];
}

const TABS: TabConfig[] = [
  {
    id: 'deposits',
    label: 'Deposits',
    categories: ['deposit.general'],
    includeKeys: [
      'deposit_company_token',
      'deposit_max_usd',
      'deposit_min_usd',
      'deposit_micro_enabled',
      'deposit_gasless_swap_enabled',
      'solana_rpc_url',
      'jupiter_api_key',
    ],
  },
  {
    id: 'tokens',
    label: 'Component',
    categories: ['deposit'],
    includeKeys: [
      'deposit_show_explainer',
      'deposit_quick_action_tokens',
      'deposit_custom_tokens',
      'deposit_custom_tokens_json',
    ],
  },
  {
    id: 'fees',
    label: 'Fees',
    categories: ['deposit'],
    includeKeys: [
      'deposit_fee_policy',
      'privacy_fee_fixed_lamports',
      'privacy_fee_percent_bps',
      'swap_fee_fixed_lamports',
      'swap_fee_percent_bps',
      'company_fee_fixed_lamports',
      'company_fee_percent_bps',
      'private_deposit_min_lamports',
    ],
  },
  {
    id: 'privacy',
    label: 'Privacy',
    categories: ['deposit.general', 'privacy', 'withdrawal'],
    includeKeys: [
      // Enable toggle
      'deposit_privacy_enabled',
      // Privacy period setting
      'privacy_period_secs',
      // Withdrawal settings (from privacy pool)
      'withdrawal_percentage',
      'withdrawal_min_lamports',
      // Partial withdrawal settings (related to privacy mixing)
      'partial_withdrawal_count',
      'partial_withdrawal_min_lamports',
    ],
    sections: [
      {
        title: 'Privacy Pool Worker',
        description: 'Background worker for processing withdrawals from the privacy cash pool.',
        keys: ['withdrawal_poll_interval_secs', 'withdrawal_batch_size'],
      },
    ],
  },
  {
    id: 'treasury',
    label: 'Treasury',
    categories: ['withdrawal', 'deposit'],
    includeKeys: [
      // Treasury wallet (separate section)
      'treasury_wallet_address',
    ],
    sections: [
      {
        title: 'Micropayment Batch Worker',
        description: 'Background worker for batching small SOL deposits together.',
        keys: ['micro_batch_threshold_usd', 'micro_batch_poll_secs'],
      },
    ],
  },
];

export interface CreditSystemSettingsProps {
  className?: string;
}

export function CreditSystemSettings({ className }: CreditSystemSettingsProps) {
  const {
    settings,
    edits,
    isLoading,
    autosaveStatus,
    autosaveError,
    error,
    fetchSettings,
    handleChange,
    getEffectiveValue,
  } = useSettingsAutosave();

  const [activeTab, setActiveTab] = useState<CreditTab>('deposits');

  useEffect(() => {
    fetchSettings();
  }, [fetchSettings]);

  // Get current tab config and filter settings
  const currentTab = TABS.find((t) => t.id === activeTab);

  // Collect all settings from the tab's categories
  const allTabSettings = useMemo(() => {
    if (!currentTab) return [];
    const allSettings: SystemSetting[] = [];
    for (const category of currentTab.categories) {
      const categorySettings = settings[category] ?? [];
      allSettings.push(...categorySettings);
    }
    return allSettings;
  }, [settings, currentTab]);

  // Get fee policy for conditional visibility
  const feePolicy = getEffectiveValue('deposit_fee_policy') || 'company_pays_all';

  const currentSettings = useMemo(() => {
    if (!currentTab) return [];

    // Apply include/exclude filters
    let filtered = allTabSettings;

    if (currentTab.includeKeys && currentTab.includeKeys.length > 0) {
      // Only include specific keys
      filtered = filtered.filter((s) => currentTab.includeKeys!.includes(s.key));
      // Sort by includeKeys order
      filtered.sort(
        (a, b) => currentTab.includeKeys!.indexOf(a.key) - currentTab.includeKeys!.indexOf(b.key)
      );
    } else if (currentTab.excludeKeys && currentTab.excludeKeys.length > 0) {
      // Exclude specific keys
      filtered = filtered.filter((s) => !currentTab.excludeKeys!.includes(s.key));
    }

    // Deposits tab: hide SOL micro payments if minimum > $10
    if (currentTab.id === 'deposits') {
      const minUsd = parseFloat(getEffectiveValue('deposit_min_usd')) || 0;
      if (minUsd > 10) {
        filtered = filtered.filter((s) => s.key !== 'deposit_micro_enabled');
      }
    }

    // Fees tab: conditionally show fee settings based on policy
    if (currentTab.id === 'fees') {
      const privacyFeeKeys = [
        'privacy_fee_fixed_lamports',
        'privacy_fee_percent_bps',
        'private_deposit_min_lamports',
      ];
      const swapFeeKeys = ['swap_fee_fixed_lamports', 'swap_fee_percent_bps'];
      const companyFeeKeys = ['company_fee_fixed_lamports', 'company_fee_percent_bps'];

      filtered = filtered.filter((s) => {
        // Always show fee policy selector
        if (s.key === 'deposit_fee_policy') return true;

        // Hide all fees if company pays all
        if (feePolicy === 'company_pays_all') return false;

        // Show privacy fees for user_pays_privacy or user_pays_all
        if (privacyFeeKeys.includes(s.key)) {
          return feePolicy === 'user_pays_privacy' || feePolicy === 'user_pays_all';
        }

        // Show swap fees for user_pays_swap or user_pays_all
        if (swapFeeKeys.includes(s.key)) {
          return feePolicy === 'user_pays_swap' || feePolicy === 'user_pays_all';
        }

        // Show company fees only for user_pays_all
        if (companyFeeKeys.includes(s.key)) {
          return feePolicy === 'user_pays_all';
        }

        return true;
      });
    }

    return filtered;
  }, [allTabSettings, currentTab, getEffectiveValue, feePolicy]);

  // Helper to get settings for a section
  const getSectionSettings = useCallback(
    (keys: string[]) => {
      return allTabSettings
        .filter((s) => keys.includes(s.key))
        .sort((a, b) => keys.indexOf(a.key) - keys.indexOf(b.key));
    },
    [allTabSettings]
  );

  // Compute external warnings for the deposits tab
  const externalWarnings = useMemo(() => {
    if (currentTab?.id !== 'deposits') return undefined;

    const warnings: Record<string, string> = {};
    const minUsd = parseFloat(getEffectiveValue('deposit_min_usd')) || 0;
    const microEnabled = getEffectiveValue('deposit_micro_enabled');
    const isMicroDisabled = microEnabled === 'false' || microEnabled === '0' || microEnabled === '';

    // Warn if micro payments disabled but minimum < $10
    if (isMicroDisabled && minUsd < 10) {
      warnings['deposit_min_usd'] =
        'SOL micro payments must be enabled for minimum deposits under $10.';
    }

    return Object.keys(warnings).length > 0 ? warnings : undefined;
  }, [currentTab?.id, getEffectiveValue]);

  // Credit system master toggle
  const creditsEnabled = getEffectiveValue('feature_credits') === 'true';
  const handleCreditsToggle = () => {
    handleChange('feature_credits', creditsEnabled ? 'false' : 'true');
  };

  if (isLoading && Object.keys(settings).length === 0) {
    return (
      <div className={`cedros-system-settings cedros-system-settings-loading ${className ?? ''}`}>
        <LoadingSpinner />
        <span>Loading settings...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className={`cedros-system-settings ${className ?? ''}`}>
        <ErrorMessage error={error.message} />
      </div>
    );
  }

  return (
    <div className={`cedros-system-settings ${className ?? ''}`}>
      <div className="cedros-settings-page-header">
        <div className="cedros-settings-page-header-content">
          <h2 className="cedros-settings-page-title">Credit System</h2>
          <p className="cedros-settings-page-description">
            Configure credit deposits, timed withdrawals, and privacy period settings.
          </p>
        </div>
        <div className="cedros-settings-page-header-actions">
          <button
            type="button"
            role="switch"
            aria-checked={creditsEnabled}
            className={`cedros-toggle ${creditsEnabled ? 'cedros-toggle-on' : 'cedros-toggle-off'}`}
            onClick={handleCreditsToggle}
          >
            <span className="cedros-toggle-track">
              <span className="cedros-toggle-thumb" />
            </span>
            <span className="cedros-toggle-label">{creditsEnabled ? 'Enabled' : 'Disabled'}</span>
          </button>
          <AutosaveStatus status={autosaveStatus} error={autosaveError} />
        </div>
      </div>

      {/* Tabs */}
      <div className="cedros-admin-tabs cedros-admin-tabs--line">
        {TABS.map((tab) => (
          <button
            key={tab.id}
            type="button"
            className={`cedros-admin-tab ${activeTab === tab.id ? 'cedros-admin-tab-active' : ''}`}
            onClick={() => setActiveTab(tab.id)}
            aria-selected={activeTab === tab.id}
            role="tab"
          >
            {tab.label}
          </button>
        ))}
      </div>

      {/* Tab content */}
      <div className="cedros-admin-tab-content" role="tabpanel">
        {currentSettings.length === 0 && !currentTab?.sections?.length ? (
          <div className="cedros-system-settings-empty">
            <p>No settings found for {currentTab?.label ?? 'this section'}.</p>
          </div>
        ) : (
          <>
            {currentSettings.length > 0 && (
              <SettingsSection
                settings={currentSettings}
                edits={edits}
                onChange={handleChange}
                externalWarnings={externalWarnings}
              />
            )}
            {/* Render additional sections */}
            {currentTab?.sections?.map((section) => {
              const sectionSettings = getSectionSettings(section.keys);
              if (sectionSettings.length === 0) return null;
              return (
                <div key={section.title} className="cedros-settings-subsection">
                  <div className="cedros-settings-subsection-header">
                    <h4 className="cedros-settings-subsection-title">{section.title}</h4>
                    {section.description && (
                      <p className="cedros-settings-subsection-description">
                        {section.description}
                      </p>
                    )}
                  </div>
                  <SettingsSection
                    settings={sectionSettings}
                    edits={edits}
                    onChange={handleChange}
                  />
                </div>
              );
            })}
          </>
        )}
      </div>
    </div>
  );
}

// Keep old export name for backwards compatibility
export { CreditSystemSettings as PrivacyCashSettings };
export type { CreditSystemSettingsProps as PrivacyCashSettingsProps };
